const aboutSlider = new Swiper('.about-slider', {
  slidesPerView: 1,
  spaceBetween: 5,
  loop: true,  
  breakpoints: {

    921: {
      slidesPerView: 1,
      spaceBetween: 30,
      navigation: {
        nextEl: '.about-slider__navigation--next',
        prevEl: '.about-slider__navigation--prev',
      },
    }
  }
});

const documentsSlider = new Swiper('.documents-slider', {
  slidesPerView: 1.5,
  spaceBetween: 10,

  breakpoints: {
    544: {
      slidesPerView: 2.5,
    },

    921: {
      slidesPerView: 4,
      spaceBetween: 20
    }
  }
});

document.querySelectorAll('.document-slide__button').forEach((btn, index) => {
  btn.addEventListener('click', () => {
    const slide = btn.closest('.swiper-slide');
    const img = slide.querySelector('img');
    if (img) {
      openModal(img.src);
    }
  });
});

const doctorsSliderText = new Swiper('.doctors__slider-text', {
  slidesPerView: 1,
  spaceBetween: 10,
  loop: true,
  
  breakpoints: {
    768: {
      spaceBetween: 20,
      navigation: {
        nextEl: '.doctors-sliders__navigation--next',
        prevEl: '.doctors-sliders__navigation--prev',
      },
    }
  }
});

const doctorsSliderPictures = new Swiper('.doctors__slider-pictures', {
  slidesPerView: 1,
  spaceBetween: 10,
  loop: true,

  breakpoints: {
    1100: {
      slidesPerView: 2,
    },
    1200: {
      slidesPerView: 2.5,
    },
    1500: {
      slidesPerView: 3,
      spaceBetween: 20,
      navigation: {
        nextEl: '.doctors-sliders__navigation--next',
        prevEl: '.doctors-sliders__navigation--prev',
      },
    }, 
  }
});

doctorsSliderText.on('slideChangeTransitionStart', function () {
  const currentIndex = doctorsSliderText.realIndex;
  if (doctorsSliderPictures.realIndex !== currentIndex) {
    doctorsSliderPictures.slideToLoop(currentIndex, 0);
  }
});

doctorsSliderPictures.on('slideChangeTransitionStart', function () {
  const currentIndex = doctorsSliderPictures.realIndex;
  if (doctorsSliderText.realIndex !== currentIndex) {
    doctorsSliderText.slideToLoop(currentIndex, 0);
  }
});

const reviewsSlider = new Swiper('.reviews-slider', {
  slidesPerView: 1,
  spaceBetween: 20,
  loop: true,
  navigation: {
    nextEl: '.reviews-slider__navigation--next',
    prevEl: '.reviews-slider__navigation--prev',
  },

  breakpoints: {
    768: {
      slidesPerView: 2,
    }
  }
});

function openModal(src) {
  const modal = document.createElement('div');
  modal.style.position = 'fixed';
  modal.style.top = '0';
  modal.style.left = '0';
  modal.style.width = '100%';
  modal.style.height = '100%';
  modal.style.backgroundColor = 'rgba(0,0,0,0.9)';
  modal.style.display = 'flex';
  modal.style.alignItems = 'center';
  modal.style.justifyContent = 'center';
  modal.style.zIndex = '3';
  
  const fullImg = document.createElement('img');
  fullImg.src = src;
  fullImg.style.maxWidth = '90%';
  fullImg.style.maxHeight = '90%';
  
  modal.appendChild(fullImg);
  modal.onclick = () => document.body.removeChild(modal);
  
  document.body.appendChild(modal);
}